html, body {
  height: 100%;
  margin: 0;
}

body {
  font-family: 'Oxygen', sans-serif;
  font-size: 16px;
  color: #454545;
}

.site-navigation {
  background-color: white;
  margin: 48px 0 0;
  padding: 20px 20vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  position: relative;
}

.site-navigation .nav-title a {
  color: #454545;
  font-size: 24px;
  text-decoration: none;
}

.nav-content .nav {
  display: flex;
  font-size: 16px;
  margin: 0;
}

.nav li {
  list-style-type: none;
  margin-right: 60px;
}

.nav li:last-child {
  margin-right: 0;
}

.nav li a {
  color: #9e9e9e;
  text-decoration: none;
  transition: 250ms color;
}

.nav li a:hover {
  color: #454545;
}

.site-main {
  margin: 0 15vw;
}

.featured-posts {
  margin-top: 40px;
}

.featured-post {
  background-size: cover;
  border-radius: 8px;
  position: relative;
  margin: auto;
  height: 420px;
  width: 100%;
  transition: transform 250ms, box-shadow 250ms;
}

.featured-post::after {
  background: linear-gradient(14deg, rgba(14,14,14,1) 0%, rgba(227, 181, 54, 0.6) 100%);
  border-radius: 8px;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.featured-post:hover {
  box-shadow: 8px 8px #e3b536;
  transform: translateY(-8px);
}

.featured-post:hover .post-card-title {
  color: #e3b536;
}

.featured-post-tag {
  background-color: #e3b536;
  border-radius: 0 4px 4px 0;
  font-size: 14px;
  font-weight: 700;
  color: white;
  display: inline-block;
  padding: 8px 8px 8px 16px;
  position:relative;
  left: 0;
  top: 24px;
  text-transform: uppercase;
  z-index: 2;
}

.featured-post-content {
  position: absolute;
  margin: 0;
  bottom: 36px;
  width: 100%;
  z-index: 2;
  color: white;
  text-align: center;
}

.featured-post-content .post-card-title {
  font-family: 'Lilita One', cursive;
  font-size: 48px;
  font-weight: 400;
  margin: 16px auto;
  width: 72%;
}

.featured-post-content .post-tagline {
  color: #cccccc;
  margin: auto;
  width: 72%;
}

.featured-post-content .post-card-excerpt {
  margin: 24px auto;
  text-align: left;
  width: 72%;
}

.latest-posts {
  margin: 40px 5vw;
}

.post-preview {
  padding: 40px 0;
  border-bottom: 1px solid #ededed;
  position: relative;
}

.post-preview:first-child {
  border-top: 1px solid #ededed;
}

.post-continue {
  color: #e3b536;
  display: none;
}

.post-preview:hover .post-title {
  color: #e3b536;
}

.post-preview:hover .post-continue {
  display: inline;
}

.post-preview .post-tagline {
  color: #9e9e9e;
  margin: 0;
}

.post-preview .post-content {
  color: #454545;
  text-decoration: none;
}

.post-preview .post-title {
  margin: 8px 0;
}

.post-preview .post-excerpt {
  margin: 0;
}

.post-tagline {
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
}

.post-tagline .dot-suffix::after {
  content: "â€¢";
  margin: 0 8px;
}

.post-tagline .primary-tag {
  font-weight: 700;
  color: #e3b536;
  text-decoration: none;
}

.post-tagline .primary-tag:hover {
  text-decoration: underline;
}

.pagination {
  font-weight: 700;
  text-align: center;
}

.newer-posts, .older-posts {
  color: #9e9e9e;
  text-decoration: none;
  transition: 250ms color;
}

.newer-posts:hover, .older-posts:hover {
  color: #454545;
}

.newer-posts {
  margin-right: 16px;
}

.older-posts {
  margin-left: 16px;
}

.site-footer {
  font-size: 14px;
  margin: 80px 0 20px;
  text-transform: uppercase;
}

.copyright {
  text-align: center;
}

.copyright a {
  color: #e3b536;
  text-decoration: none;
}

.copyright a:hover {
  text-decoration: underline;
}

.post-full {
  position: relative;
}

.post-full-header {
  margin: 40px 5vw;
  text-align: center;
}

.post-full-header .post-tagline {
  color: #9e9e9e;
  margin: 0;
}

.post-full-header .post-full-title {
  font-size: 48px;
  margin-top: 8px;
  margin-bottom: 24px;
}

.post-full-image, .post-full-image img {
  margin: 0;
  width: 100%;
}

.post-full-content {
  margin: 40px 5vw;
}

.post-content h2, .post-content h3, .post-content h4 {
  line-height: 2;
  margin-top: 32px;
}

.post-content p {
  line-height: 1.8;
}

@media (max-width: 1400px) {
  .site-navigation {
    padding: 20px 15vw;
  }

  .latest-posts {
    margin: 40px 0;
  }
}

@media (max-width: 1200px) {
  .site-navigation {
    padding: 20px 8vw;
  }

  .site-main {
    margin: 0 8vw;
  }
}

@media (min-width: 1000px) {
  .nav-menu-icon {
    display: none;
  }
}

@media (max-width: 1000px) {
  .nav-content {
    background-color: white;
    display: block;
    position: absolute;
    right: 8vw;
    top: 48px;
    opacity: 0;
    transform: translateX(280px);
    transition: opacity 250ms, transform 250ms;
    z-index: 10;
  }

  .nav-content.active {
    opacity: 1;
    transform: translateX(0);
  }

  .nav-content .nav {
    display: block;
    padding: 8px 36px;
  }

  .nav-content .nav li {
    margin: 24px 0;
  }

  .nav-menu-icon {
    display: none;
    cursor: pointer;
  }

  .nav-menu-icon.active {
    display: block;
  }
}